@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&family=Roboto:wght@100;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

html {
	overflow: hidden;
}

body {
	background: #96acbe;
	height: inherit;
	background-image: url(/minty-hours/static/media/mints.a7d92180.png);
	background-position: right bottom;
	background-repeat: no-repeat;
	background-size: 20%;
}

.project,
.ptags,
.references,
.home {
  	height: 100vh;
	padding-top: 80px;
  	-webkit-align-items: center;
  	        align-items: center;
  	-webkit-justify-content: center;
  	        justify-content: center;
  	font-size: 3rem;
    width: 95%;
    margin: auto auto;
}

table {
	display: -webkit-flex;
	display: flex;
	height: 100%;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

tbody {
	-webkit-flex: 1 1;
	        flex: 1 1;
	overflow: auto;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.intro-container {
	min-height: 300px;
	border-radius: 5px;
	border-bottom: solid #15755a 50px;
	padding: 30px;
	margin: 10px;
	background: #63a79b;
	display: grid;
	-webkit-justify-content: center;
	        justify-content: center;
	grid-gap: 10px;
	-webkit-align-items: start;
	        align-items: start;

	grid-template-columns: 1fr;
	grid-template-rows: auto auto;

	grid-template-areas:
		"img"
		"desc"
		"skills";
	
}

.proj-container {
	min-height: 300px;
	border-radius: 5px;
	border-bottom: solid #15755a 50px;
	padding: 30px;
	margin: 10px;
	background: #63a79b;
	display: grid;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	grid-gap: 10px;
	-webkit-align-items: start;
	        align-items: start;

	grid-template-areas:
		"title"
		"images"
		"desc"
		"tags";
	
}

.ptag-container {
	width: inherit;
	min-height: 300px;
	border-radius: 5px;
	border-bottom: solid #15755a 50px;
	padding: 30px;
	margin: 10px;
	background: #63a79b;
}

@media (min-width: 800px) {
	.content {
		margin-left: 260px;
	}

	.intro-container {
		grid-template-columns: 1fr 25%;

		grid-template-areas: 
		"desc img"
		"desc skills";
	}

	.proj-container {
		grid-template-columns: 1fr 25%;

		grid-template-areas:
		"title title"
		"images images"
		"desc tags";
	}

	#avatar {
		max-width: 100%;
	}
}

.intro-desc {
	font-size: 1rem;
	grid-area: desc;
	text-align: justify;
	margin: 5px;
}

.intro-img {
	grid-area: img;
}

.intro-skills {
	background: yellowgreen;
	grid-area: skills;
}

.proj-title {
	font-size: 30px;
	font-family: 'Comfortaa', cursive;
	grid-area: title;
}

.proj-desc {
	font-size: 1rem;
	font-family: 'Roboto', sans-serif;
	text-align: justify;
	grid-area: desc;
}

.proj-tags {
	grid-area: tags;
}

.proj-images {
	-webkit-align-items: center;
	        align-items: center;
	margin: 0 auto;
	-webkit-justify-content: center;
	        justify-content: center;
	grid-area: images;
}

.ptags-proj {
	font-size: 20px;
}

a {
	font-family: 'Comfortaa', cursive;
	text-decoration: underline;
	color: #15755a;
}

a:hover {
	color: blue;
}

h2 {
	background: #15755a;
	border-radius: 10px;
	padding: 7px;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	color: #c5dde1;
	font-family: 'Comfortaa', cursive;
	font-weight: 600;
	font-size: 30px;
}

.roboto {
	font-family: 'Roboto', sans-serif;
}

.inner {
	background: #c5dde1;
	border-radius: 4px;
	border: solid 4px #15755a;
	padding: 10px;
	font-family: 'Roboto', sans-serif;
	text-align: justify;
}

img {
	display: block;
	margin-left: auto;
  	margin-right: auto;
	border-radius: 7px;
	border: solid 10px #15755a;
}

#avatar {
	content: url(/minty-hours/static/media/avatar.40b4621e.jpg);
	width: 200px;
}
.navbar {
	background-color: #15755a;
	height: 80px;
	display: -webkit-flex;
	display: flex;
	position: fixed;
	z-index: 1;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: center;
	        align-items: center;
	top: 0;
  	left: 0;
  	right: 0;
}
  
.menu-bars {
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
	color: #c5dde1;
}

.nav-menu {
	background-color: #15755a;
	border-right: solid #c5dde1 10px;
	width: 250px;
	height: 100vh;
	overflow-x: hidden;
    overflow-y: auto;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 850ms;
	z-index: 2;
}
  
.nav-menu.active {
	left: 0;
	transition: 300ms;
}
  
.nav-text {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: center;
	        align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}
  
.nav-text a {
	text-decoration: none;
	color: #f5f5f5;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	padding: 0 16px;
	border-radius: 4px;
}
  
.nav-text a:hover {
	background-color: #7e899f;
	color: #211f20;
}
  
.nav-menu-items {
	width: 100%;
}
  
.navbar-toggle {
	background-color: #15755a;
	width: 100%;
	height: 80px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: center;
	        align-items: center;
}
  
span {
	margin-left: 16px;
}

.mont {
	font-family: 'Montserrat', sans-serif;
	font-weight: 700;
	letter-spacing: 2px;
}

.nav-title {
	margin-top: 15px;
	color: #f5f5f5;
	font-family: 'Comfortaa', cursive;
	font-size: 1.4rem;
}
.tag-list {
	font-size: 15px;
	background-color: #c5dde1;
	border-radius: 2px;
	padding: 10px;
	display: list-item;
	line-height: 2em;
	list-style: none;
	border: solid 4px #15755a;
}

.tag {
	font-family: 'Comfortaa', cursive;
	font-size: 14px;
	text-decoration: none;
	color: #c5dde1;
	background-color: #15755a;
	margin: 3px;
	padding: 3px;
	border-radius: 5px;
}

.tag-list a:hover {
	background: #7e899f;
	color: #211f20;
}
