.navbar {
	background-color: #15755a;
	height: 80px;
	display: flex;
	position: fixed;
	z-index: 1;
	justify-content: flex-start;
	align-items: center;
	top: 0;
  	left: 0;
  	right: 0;
}
  
.menu-bars {
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
	color: #c5dde1;
}

.nav-menu {
	background-color: #15755a;
	border-right: solid #c5dde1 10px;
	width: 250px;
	height: 100vh;
	overflow-x: hidden;
    overflow-y: auto;
	display: flex;
	justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 850ms;
	z-index: 2;
}
  
.nav-menu.active {
	left: 0;
	transition: 300ms;
}
  
.nav-text {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}
  
.nav-text a {
	text-decoration: none;
	color: #f5f5f5;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: flex;
	align-items: center;
	padding: 0 16px;
	border-radius: 4px;
}
  
.nav-text a:hover {
	background-color: #7e899f;
	color: #211f20;
}
  
.nav-menu-items {
	width: 100%;
}
  
.navbar-toggle {
	background-color: #15755a;
	width: 100%;
	height: 80px;
	display: flex;
	justify-content: flex-start;
	align-items: center;
}
  
span {
	margin-left: 16px;
}

.mont {
	font-family: 'Montserrat', sans-serif;
	font-weight: 700;
	letter-spacing: 2px;
}

.nav-title {
	margin-top: 15px;
	color: #f5f5f5;
	font-family: 'Comfortaa', cursive;
	font-size: 1.4rem;
}